// Executes when HTML-Document is loaded and DOM is ready but before page is fully loaded
document.addEventListener('DOMContentLoaded', function(): void {
  try {
    window.localStorage.setItem('ChromeExtensionBarDismiss', 'true');

    // following information will be used by clients, e.g. PPT online, to determine
    // extension existence and its capability
    window.localStorage.setItem('IsOfficeOnlineExtensionInstalled', 'true');
    window.localStorage.setItem('IsCopyPasteSupported', 'true');
  } catch (e) {
    // Do nothing if running into errors accessing or writing to localStorage
  }
});
